@import url(https://fonts.googleapis.com/css?family=Acme&display=swap);
body {
  margin: 8px;
  font-family: 'Acme', sans-serif;


  background-color: #a8d0e6;
  /* background-image: url(image/background.jpg); */
  /* background-size: cover; */
  /* padding: 10px; */
}


.links, .user{
  text-decoration: none;
  color: white;
  margin: 10px;
  font-size: 1.3em;
}
.links:hover, .user:hover {
  color: #f8e9a1;
}

.showpage {
  margin: 0 auto;
  border: 1px solid black;
  border: 10px solid #374785;
  border-radius: 10px;
}

.showpage > img {
  display: block;
  width: 100%;
  object-fit: cover;
}

h3 {
  padding-left: 10px;
  font-size: 1.5em;
  color: #f8e9a1;
}

p {
  margin: 0 auto;
  padding-left: 10px;
  margin-bottom: 15px;
}
.intro {
  font-size:1.5em;
  color: #f76c6c;
}

.playrooms-container, .users-container {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
}
.playrooms {
  border: 1px solid black;
  width: 10vw;
  height: 10vh;
}
.users {
  border: 1px solid black;
  width: 5vw;
  height: 5vh;
}

.message > h2 {
  margin-left: 10px;
  color: #f76c6c;
}

#loginLink {
  text-decoration: none;
  color: #374785;
}

#loginLink:hover {
  color: #f8e9a1;
}



.login {
  width: 25vw;
  height: 60vh;
  margin: auto;
  margin-top: 8vh;
  margin-bottom: 8vh;
  text-align: center;
  font-size: 1.5em;
  border-radius: 10px;
  background-image: url('https://images.pexels.com/photos/1762851/pexels-photo-1762851.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=750&w=1260');
  background-size: cover;
}

.login > h1 {
  padding-top: 5vh;
  color: #24305e;
}

.login > form >input {
  margin: 10px;
  height: 5vh;
  width: 17vw;
  border-radius: 5px;
  font-size: 1em;
  color: #374785;
  font-family: 'Acme', sans-serif;
  background-color:rgba(83, 78, 79, 0.49);
}

.submit {
  width: 7vw;
  height: 5vh;
  background-color: #24305e;
  font-size: 1em;
  font-family: 'Acme', sans-serif;
  color: white;
  border-radius: 5px;
}
.submit:hover{
  color: #f8e9a1;
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
}

.or {
  margin-top: 2vh;
}

.or > span {
  color: #f76c6c;
}
.or > .signup {
  padding-left: 3px;
  text-decoration: none;
  color: white;
}
.or > .signup:hover {
  color: #f8e9a1;
}

